import * as React from 'react';
import { withStyles, WithStyles } from 'material-ui/styles';
import { StyleRules } from 'material-ui/styles/withStyles';
import { Idispatcher } from './Index';
import Table, { TableBody, TableCell, TableHead, TableRow } from 'material-ui/Table';
import { ShelfState } from '../shelf/models';
import { RouteComponentProps, withRouter } from 'react-router';
import Avatar from 'material-ui/Avatar';
import IconButton from 'material-ui/IconButton';
import BackIcon from 'material-ui-icons/ArrowBack';
import BottomNavigation, { BottomNavigationAction } from 'material-ui/BottomNavigation';
import purple from 'material-ui/colors/purple';
import green from 'material-ui/colors/green';
import grey from 'material-ui/colors/grey';
import AddIcon from 'material-ui-icons/Add';
import EditIcon from 'material-ui-icons/Edit';
import DeleteIcon from 'material-ui-icons/Delete';
import Typography from 'material-ui/Typography';
import dc from '../../constants/DomainConstants';
import Dialog, {
  DialogActions,
  DialogContent,
  DialogContentText,
  DialogTitle,
} from 'material-ui/Dialog';
import Button from 'material-ui/Button';
import { Goods } from '../goods/model';
import AppBar from 'material-ui/AppBar';
import Toolbar from 'material-ui/Toolbar';

const styles: StyleRules<'table' | 'navi' | 'top'> = {
  navi: {
    backgroundColor: purple[700],
    width: '100%',
    position: 'fixed',
    bottom: 0,
  },
  top: {
    backgroundColor: green[700],
    color: grey[10],
    minHeight: window.innerHeight
  },
  table: {
    paddingTop: 57
  }
};

type ClassNames = keyof typeof styles;

const colStyle = {
  color: grey[300]
}; 

const cellStyle = {
  color: grey[300]
};

class GoodsEditTable extends React.Component<Idispatcher &
                                            ShelfState &
                                            RouteComponentProps<any> &
                                            WithStyles<ClassNames>,
                                            {
                                             openDeleteDialog: boolean,
                                             selectedGoods: Goods|null
                                            }> {
    constructor(props: any) {
      super(props);
      this.state = {
        openDeleteDialog: false,
        selectedGoods: null
      };
    }

    handleBack = () => {
      this.props.history.goBack();
    }

    linkToAddGoods = () => {
      this.props.history.push(dc.addGoods);
    }

    openDeleteDialog = (goods: Goods) => {
      this.setState({
        selectedGoods: goods,
        openDeleteDialog: true
      });
    }

    doDelete = () => {
      if (this.state.selectedGoods) {
        this.props.deleteGoods(this.state.selectedGoods);
      }
      this.setState({
        selectedGoods: null,
        openDeleteDialog: false
      });
    }

    cancelDelete = () => {
      this.setState({
        selectedGoods: null,
        openDeleteDialog: false
      });
    }

    render() {
        const {GoodsList, classes} = this.props;
        return (
          <div className={classes.top}>
          <AppBar position="fixed">
            <Toolbar>
              <IconButton onClick={this.handleBack}>
                <BackIcon style={cellStyle}/>
              </IconButton>
            </Toolbar>
          </AppBar>
            <div className={classes.table}>
              <Table >
              <TableHead>
                <TableRow>
                  <TableCell>
                    <Typography style={colStyle}>
                    イメージ
                    </Typography>
                  </TableCell>
                  <TableCell >
                    <Typography style={colStyle}>
                      なまえ
                    </Typography>
                  </TableCell>
                  <TableCell >
                    <Typography style={colStyle}>
                    ねだん
                    </Typography>
                  </TableCell>
                  <TableCell >
                    <Typography style={colStyle}>
                    へんしゅう
                    </Typography>
                  </TableCell>
                  <TableCell >
                    <Typography style={colStyle}>
                    さくじょ
                    </Typography>
                  </TableCell>
                </TableRow>
              </TableHead>
              <TableBody>
                {GoodsList.map(n => {
                  return (
                    <TableRow key={n.id}>
                      <TableCell><Avatar src={n.img}/></TableCell>
                      <TableCell >
                        <Typography style={cellStyle}>
                          {n.displayName}
                        </Typography>
                      </TableCell>
                      <TableCell >
                        <Typography style={cellStyle}>
                          {n.price}
                        </Typography>
                      </TableCell>
                      <TableCell >
                        <IconButton >
                          <EditIcon style={cellStyle}/>
                        </IconButton>
                      </TableCell>
                      <TableCell >
                        <IconButton style={cellStyle} onClick={this.openDeleteDialog.bind(this, n)}>
                          <DeleteIcon />
                        </IconButton>
                      </TableCell>
                    </TableRow>
                  );
                })}
              </TableBody>
          </Table>
          </div>
          <BottomNavigation
            className={classes.navi}
            showLabels={true}
          >
            <BottomNavigationAction 
              icon={<AddIcon style={cellStyle}/>}
              label={<Typography style={cellStyle}>ついか</Typography>}
              onClick={this.linkToAddGoods}
            />
          </BottomNavigation>
          <Dialog open={this.state.openDeleteDialog}>
            <DialogTitle>ちゅうい</DialogTitle>
            <DialogContent>
              <DialogContentText>
                しょうひん を けします <br/>
                いいですか？
              </DialogContentText>
            </DialogContent>
            <DialogActions>
              <Button onClick={this.cancelDelete} color="default">
                ×
              </Button>
              <Button onClick={this.doDelete} color="primary">
                〇
              </Button>
            </DialogActions>
          </Dialog>
          </div>
        );
    }
}

const StyledLiveContainer = withStyles<{} & ClassNames>(styles)<Idispatcher & 
                            ShelfState & RouteComponentProps<any>>(GoodsEditTable);

export default withRouter(StyledLiveContainer);



