import * as React from 'react';
import { withStyles, WithStyles } from 'material-ui/styles';
import { StyleRules } from 'material-ui/styles/withStyles';
import { Idispatcher } from './Index';
import { RouteComponentProps, withRouter } from 'react-router';
import green from 'material-ui/colors/green';
import Paper from 'material-ui/Paper';
import Button from 'material-ui/Button';
import TextField from 'material-ui/TextField';
import AddPhoto from 'material-ui-icons/AddAPhoto';
import NoDisturb from 'material-ui-icons/DoNotDisturb';
import { EditGoodsState } from './models';

const styles: StyleRules<'img' | 'bottom' | 'right'| 'paper' | 'top' | 'left' | 'photo'> = {
  paper: {
    display: 'flex',
    flexWrap: 'wrap',
    width: '80%',
    height: Math.floor(window.innerHeight * 4 / 5)
  },
  left: {
    width: '40%',
    height: '80%',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    flexDirection: 'column',
  },
  right: {
    width: '60%',
    height: '80%',
    display: 'flex',
    justifyContent: 'flex-start',
    flexDirection: 'column',
    boxSizing: 'border-box',
    padding: 30
  },
  bottom: {
    width: '100%',
    display: 'flex',
    justifyContent: 'flex-end',
    boxSizing: 'border-box',
    padding: 10
  },
  photo: {
    width: '80%',
    height: '60%',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    border: 'solid 2px #888888'
  },
  img: {
    width: '100%',
    height: '100%'
  },
  top: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    backgroundColor: green[700],
    width: '100%',
    minHeight: window.innerHeight
  }
};

type ClassNames = keyof typeof styles;

class AddGoods extends React.Component<Idispatcher &
                                            EditGoodsState &
                                            RouteComponentProps<any> &
                                            WithStyles<ClassNames>,
                                            {}> {

    loadImg = () => {
      const element = document.createElement('input') as HTMLInputElement;
      const selectedFile = (evt: Event) => {
        var file = (evt.target as HTMLInputElement).files;
        if (!file) {
          element.removeEventListener('change', selectedFile);
          return;
        }
        var reader = new FileReader();
        reader.readAsDataURL(file[0]);
        reader.onload = () => {
            this.props.setImg(reader.result);
            element.removeEventListener('change', selectedFile);
        };
      };
      element.addEventListener('change', selectedFile);
      element.type = 'file';
      element.accept = 'image/*;capture=camera';
      element.click();
    }
    
    changeName = (evt: any) => {
      this.props.setName(evt.target.value);  
    }

    changePrice = (evt: any) => {
      this.props.setPrice(evt.target.value);  
    }

    createGoods = async () => {
      await this.props.create();
      this.props.history.goBack();
    }

    back = async () => {
      this.props.history.goBack();
    }

    render() {
        const {classes, name, price, img} = this.props;
        return (
          <div className={classes.top}>
                  <Paper className={classes.paper}>
                    <div className={classes.left}>
                      <div className={classes.photo}>
                        {
                          ( () => {
                            if (img !== '') {
                              return (<img src={img} className={classes.img}/>);
                            } else {
                              return (<NoDisturb color="disabled"/>);
                            }
                          } )()
                        }
                        
                      </div>
                      <Button color="primary" onClick={this.loadImg}>
                        <AddPhoto />
                        しゃしん 
                      </Button>
                    </div>
                    <div className={classes.right}>
                    <TextField
                        label="なまえ"
                        value={name}
                        onChange={this.changeName}
                    />
                    <TextField
                        label="ねだん"
                        value={price}
                        type="number"
                        onChange={this.changePrice}
                    />            
                    </div>
                    <div className={classes.bottom}>
                      <Button onClick={this.back}>
                        キャンセル
                      </Button>
                      <Button color="primary" raised={true} onClick={this.createGoods}>
                        さくせい
                      </Button>
                    </div>
                  </Paper>
          </div>
        );
    }
}

const StyledLiveContainer = withStyles<{} & ClassNames>(styles)<Idispatcher & 
                            EditGoodsState & RouteComponentProps<any>>(AddGoods);

export default withRouter(StyledLiveContainer);



