import * as React from 'react';
import { withStyles, WithStyles } from 'material-ui/styles';
import { StyleRules } from 'material-ui/styles/withStyles';
import MenuIcon from 'material-ui-icons/Menu';
import EditIcon from 'material-ui-icons/Edit';
import Button from 'material-ui/Button';
import Drawer from 'material-ui/Drawer';
import List from 'material-ui/List';
import ListItem from 'material-ui/List/ListItem';
import { RouteComponentProps, withRouter } from 'react-router';
import ListItemText from 'material-ui/List/ListItemText';
import ListItemIcon from 'material-ui/List';
import dc from '../constants/DomainConstants';

const styles: StyleRules<'root' | 'button'> = {
    root: {
        width: '100%',
    },
    button: {
        position: 'fixed',
        bottom: 10,
        left: 10,
    }
};

type ClassNames = keyof typeof styles;

interface MenuProps {
}

class MenuComponent extends React.Component<MenuProps & RouteComponentProps<any> & WithStyles<ClassNames>, 
    {open: boolean}> {

    constructor(props: any) {
        super(props);
        this.state = {
            open: false
        };
    }

    closeDrawer = () => {
        this.setState({
            open: false
        });
    }

    oepnDrawer = () => {
        this.setState({
            open: true
        });
    }

    handleEditTable = () => {
        this.props.history.push(dc.goods);
        this.closeDrawer();
    }

    render() {
        const {classes} = this.props;
        return (
        <div className={classes.root}>
            <Button 
                fab={true} 
                color="primary" 
                aria-label="menu" 
                className={classes.button}
                onClick={this.oepnDrawer}
            >
                <MenuIcon />
            </Button>
            <Drawer open={this.state.open} onClose={this.closeDrawer}>
          <div
            tabIndex={0}
          >
            <List>
            <ListItem button={true} onClick={this.handleEditTable}>
                <ListItemIcon>
                    <EditIcon />
                </ListItemIcon>
                <ListItemText primary="しょうひん　ついか・へんしゅう" />
            </ListItem>
            </List>
          </div>
        </Drawer>
        </div>
        );
    }
}

const styled = withStyles<{} &  ClassNames>(styles)<RouteComponentProps<any> & MenuProps>(MenuComponent);

export default withRouter(styled);


